<template>
    <div id="results">
        <p>
            {{this.results}}
            {{this.updateKey}}
        </p>
    </div>
</template>

<script>
export default {
    data: function() {
        return {
            updateKey: 0
        }
    },
    name: "ResultsComponent",
    props: {
        results: Array
    },
    watch: {
        results: function(newVal) {
            this.results = newVal;
            this.updateKey += 1;
            console.log(this.results);
        }
    }
}
</script>

