import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

import Home from '../views/Home.vue'
import Results from '../views/Results.vue'


export default new VueRouter({
    routes: [
        {
            path: "/",
            name: 'Home',
            component: Home,
            children: [
                {
                    path: 'results?pitcher=:pitcherId&batter=:batterId',
                    name: 'Results',
                    component: Results
                }
            ]
        }
    ]
})