<template>
    <div id="results">
        <p>HELLO</p>
        <ResultsComponent v-bind:results='this.results' 
                          :key='updateKey'
        />
    </div>
</template>

<script>
import axios from 'axios';

import ResultsComponent from "../components/ResultsComponent";

export default {
    name: "Results",
    data: function() {
        return {
            results: []
        }
    },
    components: {
        ResultsComponent
    },
    mounted() {
        axios.get('https://api.blaseball-reference.com/v1/events', 
                { params: { pitcherId: this.$route.params.pitcherId, 
                            batterId: this.$route.params.batterId} })
                 .then(response => {
                     if(response.status == 200) {
                         this.results = response['data']['results']
                         console.log("got results")
                     }
                     else {
                         console.log(response.error);
                     }
                 })
    }
}
</script>

